/* === Main Layout === */
.main-container {
  min-height: 100vh;
  display: flex;
  background: #f3f4f6; /* light gray */
  font-family: Arial, sans-serif;
}

/* === Left Side (Decoration Area) === */
/* === Left Container === */
.left-container {
  width: 50%;
  background: linear-gradient(135deg, #1e3a8a, #3b82f6);
  background-size: 200% 200%;
  animation: gradientMove 8s ease-in-out infinite;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 60px;
  color: white;
  text-align: left;
  position: relative;
  box-shadow: inset 0 0 40px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

/* Gradient Animation */
@keyframes gradientMove {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Title */
.left-container .title {
  font-size: 40px;
  font-weight: 800;
  margin-bottom: 20px;
  line-height: 1.2;
  opacity: 0;
  animation: fadeSlideUp 1s ease forwards;
}

/* Description */
.left-container .discription {
  font-size: 18px;
  opacity: 0;
  max-width: 420px;
  line-height: 1.6;
  margin-bottom: 40px;
  animation: fadeSlideUp 1.4s ease forwards;
}

/* Developer name */
.left-container .developer-name {
  margin-top: auto;
  font-size: 16px;
  opacity: 0;
  font-weight: 600;
  animation: fadeSlideUp 1.8s ease forwards;
}

/* Fade + slide animation */
@keyframes fadeSlideUp {
  0% {
    opacity: 0;
    transform: translateY(25px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Glow circle behind */
.left-container::before {
  content: "";
  position: absolute;
  top: 20%;
  left: 10%;
  width: 250px;
  height: 250px;
  background: rgba(255, 255, 255, 0.15);
  filter: blur(120px);
  border-radius: 50%;
  z-index: 0;
  animation: floatGlow 6s ease-in-out infinite alternate;
}

/* Glow floating animation */
@keyframes floatGlow {
  0% { transform: translate(0, 0) scale(1); }
  100% { transform: translate(20px, -20px) scale(1.2); }
}

.left-container > * {
  position: relative;
  z-index: 2;
}

/* Responsive */
@media (max-width: 900px) {
  .left-container {
    width: 100%;
    padding: 40px;
    text-align: center;
  }

  .left-container .developer-name {
    margin-top: 20px;
  }
}


/* You can put image or text inside left-container if needed */

/* === Right Side === */
.right-container {
  width: 50%;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 60px 50px;
  text-align: center;
  box-shadow: 0 10px 35px rgba(0,0,0,0.12);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border-left: 1px solid #e5e7eb;
  background: linear-gradient(180deg, #ffffff 0%, #f9f9f9 100%);
}

.right-container h2 {
  font-size: 32px;
  font-weight: 700;
  color: #2563eb;
  margin-bottom: 20px;
}

/* === Error Message === */
.error {
  background: #ffebeb;
  padding: 12px;
  border-left: 5px solid #d32f2f;
  color: #b71c1c;
  border-radius: 6px;
  margin-bottom: 15px;
  font-weight: 600;
}

/* === Form === */
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 15px;
}

input {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  border: 1px solid #cbd5e1;
  border-radius: 8px;
  outline: none;
  transition: 0.2s;
}

input:focus {
  border-color: #2563eb;
  box-shadow: 0 0 5px rgba(37,99,235,0.4);
}

button {
  padding: 14px;
  background: #2563eb;
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 600;
}

button:hover {
  background: #1d4ed8;
}

/* === Register Link === */
.right-container p {
  margin-top: 10px;
  font-size: 15px;
}

.right-container a {
  color: #2563eb;
  font-weight: 600;
}

/* === Responsive === */
@media (max-width: 900px) {
  .main-container {
    flex-direction: column;
  }
  .left-container, .right-container {
    width: 100%;
  }
  .right-container {
    padding: 30px;
  }
}
/*auth-container*/
.auth-container {
  width: 100%;
  max-width: 380px;
  background: #ffffff;
  padding: 35px 30px;
  border-radius: 20px;
  box-shadow: 0px 8px 25px rgba(0, 0, 0, 0.08);
  border: 1px solid #e6e6e6;
  animation: fadeIn 0.4s ease;
}

.auth-container h2 {
  text-align: center;
  margin-bottom: 22px;
  font-size: 26px;
  color: #222;
}

/* Inputs */
.auth-container input {
  width: 100%;
  padding: 12px 14px;
  margin-bottom: 15px;
  border: 1.5px solid #dcdcdc;
  border-radius: 10px;
  font-size: 15px;
  transition: 0.2s;
}

.auth-container input:focus {
  border-color: #7a5cff;
  box-shadow: 0 0 5px rgba(122, 92, 255, 0.3);
  outline: none;
}